import React from "react";
import ReactHtmlParser from 'react-html-parser';
import en from './en-US.json';
import sk from './sk-SK.json';
import cz from './cs-CZ.json';
import pl from './pl-PL.json';
import de from './de-AT.json';

const Context = React.createContext();

export class LocalizationProvider extends React.Component {
  constructor(props) {
    super(props);

    this.locales = { 
      'en-US': { ...en, ...props['en-US'] },
      'sk-SK': { ...sk, ...props['sk-SK'] },
      'cs-CZ': { ...cz, ...props['cs-CZ'] },
      'pl-PL': { ...pl, ...props['pl-PL'] },
      'de-AT': { ...de, ...props['de-AT'] }
    };

    this.state = {
      locale: props.locale || 'en-US'
    };
  }

  translate = locale => (key) => {
    const k = key.split('.');

    let value = this.locales[locale][k[0]];
    if(k.length > 1) {
      value = value[k[1]];
    }
    return ReactHtmlParser(value);
  }

  render() {
    const { children } = this.props;

    return (
      <Context.Provider
        value={{
          locale: this.state.locale,
          translate: this.translate(this.state.locale)
        }}
      >
        {children}
      </Context.Provider>
    );
  }
}

export const withLocalization = (WrappedComponent) => {
  return (props) => {
    return (
      <Context.Consumer>
        {x =>
          <WrappedComponent
            {...props}
            {...x}
          />
        }
      </Context.Consumer>
    );
  }
};